import "./styles.css";
import React, { useState } from "react";
export default function App() {
  var [books, setbooks] = useState("");
  var [fictionBooks, setfictionBooks] = useState("");
  var [businessBooks, setbusinessBooks] = useState("");

  function jsBooksHandler() {
    var books = ["Eloquent , ", " Javascript:The Good Part"];
    setbooks(books);
  }
  function fictionBooksHandler() {
    var fictionBooks = ["Invisible Man , ", "One Hundered Year of Solitude"];
    setfictionBooks(fictionBooks);
  }
  function businessBooksHandler() {
    var businessBooks = ["Think and Grow Rich , ", "Rich Dad Poor Dad"];
    setbusinessBooks(businessBooks);
  }
  return (
    <div className="App">
      <h1>Good books </h1>
      <p>Checkout my favourite books. Select a genere to get started</p>
      <button onClick={jsBooksHandler}>javascript</button>
      <button onClick={fictionBooksHandler}>Fiction</button>
      <button onClick={businessBooksHandler}>business</button>

      <hr />
      <h3>{books}</h3>
      <h3>{fictionBooks}</h3>
      <h3>{businessBooks}</h3>
    </div>
  );
}
